.header {
  position: fixed;
  display: flex;
  width: 100%;
  background-color: rgba($color-black-5, 0.9);;
  padding: 28px 0;

  .container {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    flex-shrink: 0;
    margin: 0 20px 0 0;
    width: 247px;
    height: 35px;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
